.custom-select-container {
  position: relative;
}

.custom-select-container.is-disabled {
  opacity: 0.333;
}

.custom-select-opener {
  position: relative;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  width: fit-content;
  min-width: 75px;
  padding: 8px;
  padding-right: 30px;
  cursor: pointer;
  color: #303030;
  background-color: transparent;
}
.custom-select-opener::before {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  content: "";
  width: 12px;
  height: 7px;
  background-image: url("../assets/img/icon-arrow-down.svg");
  background-size: contain;
  background-repeat: no-repeat;
  transition: transform var(--main-transition);
}

.custom-select-container select {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.custom-select-panel {
  position: absolute;
  height: 0;
  overflow: hidden;
  background-color: white;
  top: calc(100% + 10px);
  left: 0;
  z-index: 1;
  width: 100px;
  box-shadow: 0px 10px 20px rgba(42, 52, 79, 0.1);
}
.custom-select-panel::-webkit-scrollbar {
  width: 4px;
}

.custom-select-container.is-open .custom-select-panel {
  width: fit-content;
  min-width: 100px;
  height: fit-content;
  max-height: 150px;
  overflow-y: auto;
  z-index: 101;
}

.custom-select-option {
