  color: #303030;
  background-color: white;
  transition: all var(--main-transition);
}
.custom-select-option::before {
  display: none;
}
.custom-select-option:not(:last-child) {
  margin-bottom: 3px;
}

.custom-select-option.has-focus {
  color: var(--white);
  background-color: #303030;
}

