  margin-right: 15px;
}
.filter-opener svg {
  transition: transform var(--main-transition);
}
.filter-opener.active svg {
  transform: rotate(180deg);
}
.filter-container {
  position: absolute;
  top: calc(100% + 20px);
  left: 0;
  width: 280px;
  height: fit-content;
