.invalid-animation, .invalid-control, .form__content input.js-invalid-control, .form__content fieldset:first-child input.js-invalid-control {
  animation: shake 0.5s 0.1s linear;
}

.disabled, .form__content input:disabled + label, .form__content input:disabled + label::before, .form__content input:disabled {
  opacity: 0.4;
  cursor: default;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0);
  }
}
.invalid-control, .form__content input.js-invalid-control, .form__content fieldset:first-child input.js-invalid-control {
  color: var(--accent);
}
.invalid-control::placeholder, .form__content input.js-invalid-control::placeholder, .form__content fieldset:first-child input.js-invalid-control::placeholder {
  color: var(--accent);
}
.invalid-control + label, .form__content input.js-invalid-control + label, .form__content fieldset:first-child input.js-invalid-control + label {
  color: var(--accent);
}

.form__content {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 30px;
  margin-bottom: 40px;
}
.form__content fieldset:first-child {
